body{
padding-left: 50px;
padding-right: 50px; 
}

.transactionhistory{
margin: 40px auto;
width: 100%;

}


table, th, td {
height: 35px;
border: 1px solid rgb(147, 142, 142, 0.2);
}

table {
    table-layout: fixed;
    width: 100%;
    border-collapse: collapse;
    border: 3px solid rgb(107, 101, 101, 0.2);
  }




th {
text-transform: uppercase;    
color:white;
background-color: deepskyblue;
}

td{
text-align: center;
 font-size: 15px;
 font-weight: 400;
 color:darkgrey;
}
td:first-child{
padding-left:130px;
text-align: left;
}

tbody tr:nth-child(odd) {
    background-color:white;
  }
  
  tbody tr:nth-child(even) {
    background-color: rgb(195, 187, 187, 0.5);
  }
  
.type::first-letter{   
text-transform: capitalize;   
}
